<template>
    <div class="start">
        <h2 class="start__title">Параметры</h2>
        <div class="start__range range">
            <div class="range__container">
                <p class="range__text">Количество цифр</p>
                <RangeSlider 
                    class="range__slider"
                    min="2"
                    max="5"
                    step="1"
                    v-model="digits"
                />
            </div>            
            <span class="range__output">{{ digits }}</span>
        </div>

        <div class="range">
            <div class="range__container">
                <p class="range__text">Время показа (сек.)</p>
                <RangeSlider 
                    class="range__slider"
                    min="0.2"
                    max="2"
                    step="0.1"
                    v-model="time"
                />
            </div>            
            <span class="range__output">{{ time.toFixed(1) }}</span>
        </div>
        <button class="start__start-btn button"
                @click="start"
                >Старт</button>
    </div>
</template>

<script>
import RangeSlider from 'vue-range-slider'
import 'vue-range-slider/dist/vue-range-slider.css'
export default {
    data() {
        return {
            digits: 2,
            time: 0.2,
        }
    },
    components: {
        RangeSlider
    },
    methods: {
        start() {
            this.$emit('set-tab', 'Number')
            this.$emit('start', { digits: this.digits, time: this.time})
        }
    }
}
</script>
<style>
@import '../assets/style/button.css';
@import '../assets/style/range.css';

.start__title {
    text-align: left;
}

.start__start-btn {
    width: 100%;
    margin-top: 40px;
}

</style>
