<template>
    <div class="result">
        <h2 class="result__title"> {{ title }} </h2>
        <form action="#" class="result__form form">
            <input class="form__input" type="text"
                    id="userNum"
                    :maxlength="params.digits"
                    :class="{ correct: status === 'correct', incorrect: status === 'incorrect'}"
                    v-model="number"
                    >
            <button type="submit"
                    class="form__submit button"
                    @click.prevent="check"
                    >Проверить</button>            
        </form>

        <div class="result__menu">
            <button class="result__menu-btn" @click="$emit('set-tab', 'Number')">Повторить</button>
            <button class="result__menu-btn" @click="$emit('set-tab', 'Start')">Заново</button>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            number: null,
            status: null,
            title: 'Введите число'
        }
    },
    props: ['params'],
    mounted() {
        document.getElementById('userNum').focus()
    },
    methods: {
        check() {
            if (+this.number === this.params.generatedNumber) {
                this.status = 'correct'
                this.title = 'Верно!'
            } else {
                this.status = 'incorrect'
                this.title = 'Неверно!'
            }
        }
    }
}
</script>

<style scoped>
@import '../assets/style/button.css';
@import '../assets/style/form.css';

.result__menu {
    margin-top: 40px;
    display: flex;
    justify-content: space-between;
}

.result__menu-btn {
    color: #21FB92;
    border: none;
    background-color: transparent;
    font-size: 16px;
    cursor: pointer;
    font-weight: bold;
    text-transform: uppercase;
}

.result__menu-btn:hover {
    text-decoration: underline;
}

.correct {
    background-color: #21FB92;
    transition: 0.5s;
    color: #ffffff;
}

.incorrect {
    background-color: #f0544f;
    transition: 0.5s;
    color: #ffffff;
}
</style>