<template>
  <div id="app">
    <header>
      <h1>Мини тренажер "Запомни число"</h1>
    </header>
    <component
      @set-tab="setTab"
      @start="start"
      @generate="getNumber"
      class="content"
      v-bind:is="currentTabComponent"
      v-bind:params="params"
    >
    </component>
  </div>
</template>

<script>
import TabStart from "../src/components/TabStart";
import TabNumber from "../src/components/TabNumber";
import TabResult from "../src/components/TabResult";
import generateNumberByDigits from "../src/assets/js/generateNumberByDigits";
export default {
  name: "App",
  data() {
    return {
      currentTab: "Start",
      params: {        
        digits: null,
        time: null,
        generatedNumber: null
      },
    };
  },
  components: {
    TabStart,
    TabNumber,
    TabResult,
  },
  computed: {
    currentTabComponent: function () {
      return "Tab" + this.currentTab;
    },
  },
  methods: {
    setTab(tabName) {
      this.currentTab = tabName
    },
    start(data) {
      this.params.digits = data.digits;
      this.params.time = data.time;
    },
    getNumber(digits) {
      this.params.generatedNumber = generateNumberByDigits(digits)
    }
  },
};
</script>

