<template>
    <div class="number">
        <h2 class="number__title">{{ title }}</h2>
        <div class="number__content">
            <span class="number__status">{{ status }}</span>
        </div>
    </div>
</template>

<script>
export default {
    props: ['params'],
    data() {
        return {
            countdown: 2,
            title: null,
            status: null
        }
    },
    methods: {        
        startTimer() {
            this.title = 'Приготовьтесь...'
            let time = this.countdown
            let timer = setInterval(() => {
                if (time === 0) {
                    this.$emit('generate', this.params.digits)
                    clearInterval(timer)
                    this.title = 'Число'
                    this.status = this.params.generatedNumber

                    setTimeout(() => {
                        this.$emit('set-tab', 'Result')
                    }, this.params.time * 1000)
                } else {
                    time--
                    this.status = time
                }                
            }, 1000)            
        }
        
    },
    mounted() {
        this.status = this.countdown
        this.startTimer()
    }
}
</script>

<style scoped>
.number__content {
    margin-top: 40px;
}
.number__status {
    font-size: 72px;
}
</style>